@import url(https://fonts.googleapis.com/css?family=Kameron);

.App {
    text-align: center;
    max-width: 100%;
    min-height: 100vh;
    margin: none;
    font-family: 'Kameron', serif;
}
.container {
    display: flex;
    flex-direction: column;
    background: var(--background_container);
    padding: var(--padding_container);
    width: var(--width_container);
    max-width: 100%;
    margin: 10vmin auto;
    padding: calc(var(--button_margin) * 4) 0;
    border: 10px solid var(--border_color);
    box-shadow: 2px 3px darkgrey;
}

@media (max-width: 420px) {
    .container {
        border-left: none;
        border-right: none;
        margin-top: 0;
    }
}
.inputs {
    display: grid;
    grid-template-areas: "cl bs sr di" "se ei ni pr" "fo fi si mi" "on tw th pl" "de ze mo eq";
    margin: var(--button_margin) auto;
}

.button {
    width: var(--button_size);
    height: var(--button_size);
    margin: var(--button_margin);
    background: var(--button_background);
    border: none;
    border-radius: var(--button_radius);
    color: var(--color_app);
    font-size: 2rem;
    text-shadow: 1px 1px var(--result_color);
    font-family: var(--font_primary);
    box-shadow: 1px 1px var(--border_color);
}
:root {
    /*color schemes*/
    --background_app: lightgrey;
    --color_app: white;
    --background_container: #d3560e;
    --button_background: linear-gradient(#ff8000, #dd6f00);
    --border_color: #a50;
    --input_color: #121212;
    --result_color: #020202;
    --display_background: whitesmoke;
    /*font families*/
    --font_primary: 'Kameron', serif;
    /*sizes*/
    --width_app: 100%;
    --padding_app: 0px;
    --padding_container: 0px;
    --width_container: 400px;
    --button_size: calc((375px/4) - calc(var(--button_margin) * 3));
    --button_margin: 5px;
    --display_width: calc((var(--button_size) * 4) + (var(--button_margin) * 5));
    --display_height: 70px;
    --display_padding: 5px;
    --button_radius: 6px;
}
.Clear {
    grid-area: cl;
}

.Backspace {
    grid-area: bs;
}

.square_root {
    grid-area: sr;
}

.add {
    grid-area: pl;
}

.subtract {
    grid-area: mi;
}

.multiply {
    grid-area: pr;
}

.modulus {
    grid-area: mo;
}

.equals {
    grid-area: eq;
}

.nine {
    grid-area: ni;
}

.eight {
    grid-area: ei;
}

.seven {
    grid-area: se;
}

.six {
    grid-area: si;
}

.five {
    grid-area: fi;
}

.four {
    grid-area: fo;
}

.three {
    grid-area: th;
}

.two {
    grid-area: tw;
}

.one {
    grid-area: on;
}

.zero {
    grid-area: ze;
}
* {
    margin: 0;
    padding: 0;
    max-width: 100%;
}

html {
    font-size: 17px;
    letter-spacing: 2px;
}

body {
    background: var(--background_app);
}
.display {
    width: var(--display_width);
    box-sizing: border-box;
    padding: var(--display_padding);
    text-align: right;
    height: auto;
    margin: auto;
    background: var(--display_background);
    border-radius: var(--button_radius);
}

.input_display {
    color: var(--input_color);
}

.result {
    color: var(--result_color);
    font-size: 1.5rem;
    line-height: 0.8;
}
.footer {
    position: absolute;
    bottom: 0px;
    width: 100%;
    font-size: 0.7rem;
}
